*,
*::after,
*::before{
  margin: 0;
  padding: 0;
  box-sizing: inherit;
  //border: 1px solid red;
}

html{
  //This defines what 1rem is
  font-size: 62.5%;
  scroll-behavior: smooth;



  @include respond(tab-land){
    font-size: 65.25%;
  }

  @include respond(tab-port){
    font-size: 50%;
  }

  /* @include respond(phone){
    font-size: 30%;
   }*/

  @include respond(big-desktop){
    font-size: 75%;
  }
}

body {
  box-sizing: border-box;
  padding: 3rem;

  @include respond(tab-land){
   padding: 0;
  }

  @include respond(tab-port){
  padding: 0;
  }

  @include respond(phone){
    padding: 0;
  }

  @include respond(big-desktop){
  }
}